.Item-container
{
    background-size: 100% 100%;
    height: 100%;
    position:relative
}

.Item-image
{
    height: 100%;
    width: 100%;
}

.Item-content
{
    justify-self: flex-end;
    height: 20%;
    width: 100%;
    background-color: rgba(0,0,0, 0.3);
    float: bottom;
    position: absolute;
    bottom: 0;
    left: 0px;
    right: 0px;
    font-size: 2vw;
}


