.Detail-container
{
    width: 100%;
    height: auto;
    min-height: 70vh;
    min-width:  100%;
    max-width: 100%;
    padding-left: 5%;
}

.Detail-descr
{
    width: 100%;
    max-width: 100%;
    min-width: 100%;
    display: -ms-flexbox;
    display: flex;
    padding-left: 5%;
    margin: 5vh 0px;

}

.Detail-info
{
    width: 70%;
    max-width: 70%;
    min-width: 70%;
    border: 0.1vw solid #000;
}

.Detail-img
{
    background-size: 100% 100%;
    min-height: 60vh;
    height: 60vh;
    max-height: 60vh;
    width: 45%;
    margin-right: 2.5%;
}
.Detail-content
{
    height: 60vh;
    width: 45%;
    overflow: visible;
    margin-right: 2.5%;
    font-size: 3vh;
}

.Detail-descr-chapter
{
    width: 100%;
    max-width: 100%;
    min-width: 100%;
    display: block;
    padding-left: 5%;
    padding-right: 5%;
    margin-bottom: 5vh;
}
.Detail-title
{
    border: 0px double deepskyblue;
    border-bottom-width: 0.5vw;
    border-left-width: 0.2vw;
    width: 100%;
    -ms-flex-item-align: center;
        align-self: center;
    margin-bottom: 5vh;
}



.Detail-title-text
{
    font-family: 'Arial';
    font-weight: bold;
    font-size: 2em;
    margin-left: 0.2vw;
}

.Detail-title-text:hover
{
    font-family: 'Arial';
    font-weight: bold;
    font-size: 2em;
    color: dodgerblue;
    margin-left: 0.2vw;
}

.Detail-chapter-text
{
    font-family: 'Arial';
    font-weight: bold;
    font-size: 1.8em;
    color: dodgerblue;
    margin-left: 2vw;
}



.Detail-chapter-text:hover
{
    font-family: 'Arial';
    font-weight: bold;
    font-size: 1.8em;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
    color: mediumturquoise;
    margin-left: 2vw;
}


