.Grid-container
{
    width: 70vw;
    height: auto;
    padding-left: 2vw;
    padding-right: 2vw;
    display: block;
    border-color: aqua;
}


.Book-item
{
    min-width: 15vw;
    min-height: 18vw;
    max-width: 15vw;
    max-height: 18vw;
    width: 15vw;
    height: 18vw;
    background-color: transparent;
    border-bottom-color: transparent;
    border-width: 0;
    display: block;
}


