.Image-container
{
    margin: 10vw auto 0;
    display: block;
    border: 2vw solid #e9e9e9;
    border-radius: 1vw;
    max-width: 95%;
    vertical-align: middle;
}

#topBtn
{
    width: 4vw;
    height: 4vw;
    background-color: chartreuse;
    position: -webkit-sticky;
    position: sticky;
    display: none;
    right: 1vw;
    bottom: 1vh;
    float: right;
    border-radius: 2vw;
}

#topBtn:active
{
    width: 4vw;
    height: 4vw;
    background-color: transparent;
    position: -webkit-sticky;
    position: sticky;
    right: 1vw;
    bottom: 1vh;
    float: right;
    border-radius: 2vw;

}

.Page-content
{
    width: auto;
    background-color: #222222;
}


