.Home {
    width: 100%;
    height: auto;
    margin: 0px;
    padding: 0px;
    z-index: 999;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}


.App-title {
  font-size: 1.5em;
}

.App-bar
{
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: #222;
    height: 20%;
    width: 100%;
    max-width: 100vw;
    padding: 20px 0px;
    margin-bottom: 50px;
    float: top;
}


.App-footer
{
    background-color: #222;
    margin-top: 50px;
    height: 100px;
    width: 100%;
    padding: 20px 0px;
    color: white;
    margin-top: 50px;
    float: bottom;
}


.App-bar-button{
    background-color: transparent;
    margin-left: 20px;
    height: 100%;
    border-color: transparent;
    font-family: 'Shrikhand', 'Arial';
}

.App-bar-text
{
    color: white;
    font-size: 30px;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
}


.App-bar-text:hover
{
    color: gainsboro;
    font-size: 30px;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
}


.App-footer-text
{
    color: white;
    font-size: 30px;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
    float: left;
}



.App-intro {
  font-size: large;
}

.clearfix {
    zoom: 1
}

.clearfix::after {
    content: ".";
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}

.Carousel
{
    width: 100%;
    height: 60vh;
    max-width: 100%;
    margin-bottom: 20vh;
    max-height: 60vh;
    overflow: hidden;
}

.Carousel-img
{
    background-size: 100% 100%;
    height: 60vh;
    width: 100%;
}

.Carousel-caption
{
    text-shadow : 5px 5px 5px black;
    font-size: 5vw;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
    color: white;
}

.Carousel-caption:hover
{
    text-shadow : 5px 5px 2px black;
    font-size: 5vw;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
    color: white;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



